import React from 'react';
import './record.styl';
import {Button} from '../Button'

export default function Record(props) {
    const { items, handleDelete } = props;
    if (items && items.length > 0){
        return (
            <table className="list-wrapper">
                <thead>
                    <tr className="list-header">
                        <th className="list-cell">First Name</th>
                        <th className="list-cell">Last Name</th>
                        <th className="list-cell">Phone</th>
                        <th className="list-cell">Age</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {items.map((item, index) =>{
                            return(
                                <tr className="list-row" key={index}>
                                    <td className="list-cell">{item.firstName}</td>
                                    <td className="list-cell">{item.lastName}</td>
                                    <td className="list-cell">{item.phone}</td>
                                    <td className="list-cell">{item.age}</td>
                                    <td><Button className="delete" onClick={() => handleDelete(item.uid)}>Delete user</Button></td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        )
    } else {
        return (<div className="list-wrapper">There is no users yet</div>)
    }
}